export default {
  getFoodsList: (state) => state.foodsList,
  getCurUserName: (state) => state.curUserName,
  getFind: (state) => state.find,
  getGoods: (state) => state.goods,
  getOrder: (state) => state.order,
  getRatings: (state) => state.ratings,
  getSeller: (state) => state.seller,
  getStore: (state) => state.store,
  getUserList: (state) => state.userList,
  foodsListLength (state) {
    return state.foodsList.length
  },
  totalCount (state) {
    let count = 0
    state.foodsList.forEach((item) => {
      count += item.count
    })
    return count
  },
  totalPrice (state) {
    let price = 0
    state.foodsList.forEach((item) => {
      price += item.count * item.min_price
    })
    return price
  }
}



